import { createRef, useRef, useState } from 'react'
import {
  faInfo,
  faContactBook,
  faLink,
  faSuitcase,
  faUser,
} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { NavLink } from 'react-router-dom'
import './sidebar.scss'
import { useEffect } from 'react'

/**
 * https://stackoverflow.com/questions/74394457/unable-to-move-indicator-to-active-link-with-navlink-in-react-router-6-4-3
 */

const links = [
  {
    to: '/about',
    icon: faInfo,
    label: 'About',
  },
  {
    to: '/portfolio',
    icon: faSuitcase,
    label: 'Works',
  },
  {
    to: '/',
    icon: faUser,
    label: 'Home',
  },
  {
    to: '/contact',
    icon: faContactBook,
    label: 'Contact',
  },
  {
    to: '/links',
    icon: faLink,
    label: 'Links',
  },
]

const SidebarItem = ({ isActive, link, setIsActive }) => {
  useEffect(() => {
    if (isActive) setIsActive()
  }, [isActive, setIsActive])

  return (
    <>
      <FontAwesomeIcon className="icon" icon={link.icon} />
      <span className="text">{link.label}</span>
    </>
  )
}

const Sidebar = () => {
  const markerRef = useRef()
  const linkRefs = useRef([])
  const [anchorEl, setAnchorEl] = useState(null)

  linkRefs.current = links.map((_, i) => linkRefs.current[i] ?? createRef())

  useEffect(() => {
    if (anchorEl) {
      markerRef.current.style.left = anchorEl.offsetLeft + 'px'
      markerRef.current.style.width = anchorEl.offsetWidth + 'px'
    }
  }, [anchorEl])

  return (
    <>
      <div className="navigation" id="footer">
        <ul>
          {links.map((link, index) => (
            <li key={link.to}>
              <NavLink ref={linkRefs.current[index]} to={link.to}>
                {({ isActive }) => (
                  <SidebarItem
                    {...{
                      isActive,
                      link,
                      setIsActive: () =>
                        setAnchorEl(linkRefs.current[index].current),
                    }}
                  />
                )}
              </NavLink>
            </li>
          ))}
          <div ref={markerRef} id="marker"></div>
        </ul>
      </div>
    </>
  )
}

export default Sidebar
