const ImageUploadField = ({ name, handleChange }) => {
  const retrievePath = (e) => {
    const file = e.target.files[0]
    const target = {}
    const reader = new FileReader()
    reader.readAsDataURL(file)
    reader.onloadend = (e) => {
      target.name = name
      target.value = reader.result
      target.logoName = file.name
      handleChange({ target })
    }
  }

  return (
    <div className="buttn">
      <label for="actual-btn" id='buttn'>Choose logo file</label>
      <input
        id="actual-btn"
        hidden
        className="image__input"
        type="file"
        accept="image/*"
        onChange={retrievePath}
      />
    </div>
  )
}

export default ImageUploadField
