import { createContext } from 'react'
import './portfolio.scss'
import InputField from './QRCode/components/inputField'
import InputColor from './QRCode/components/inputColor'
import { useState } from 'react'
import { QRCode } from 'react-qrcode-logo'
import ImageUploadField from './QRCode/components/imageUploadField'
import html2canvas from 'html2canvas'

export const InputContext = createContext()

const Portfolio = () => {
  const [inputValue, setInputValue] = useState({
    url: '',
    color: '',
    logo: '',
  })

  const value = {
    inputValue,
    setInputValue,
  }

  // import logo chosen by the user and set it to the logoImage property of the QRCode object
  const handleChange = (e) => {
    setInputValue({ ...inputValue, logo: e.target.value })
  }

  const handleColorChange = (e) => {
    setInputValue({ ...inputValue, color: e.target.value })
  }

  const handleDownload = () => {
    html2canvas(document.querySelector('#react-qrcode-logo')).then(function (
      canvas
    ) {
      const link = document.createElement('a')
      link.download = 'react-qrcode-logo.png'
      link.href = canvas.toDataURL()
      link.click()
    })
  }

  return (
    <div className="portfolio">
      <div className="portfolio__container">
        <div className="portfolio__container__item">
          <InputContext.Provider value={value}>
            <InputField />
            <InputColor handleChange={handleColorChange} />
            <ImageUploadField name="logo" handleChange={handleChange} />
            <QRCode
              value={inputValue.url}
              size={280}
              ecLevel="Q"
              imageSettings={{
                src: inputValue.logo,
                height: 300 * 0.1,
                width: 300 * 0.1,
                excavate: true,
              }}
              logoImage={inputValue.logo}
              fgColor={inputValue.color}
            />
            <button
              type="button"
              onClick={handleDownload}
              style={{ margin: '20px' }}
            >
              Download QR Code
            </button>
          </InputContext.Provider>
        </div>
      </div>
    </div>
  )
}

export default Portfolio
